.recipes {
  width: 1405px;
  margin-right: 14px;
  background-color: rgba(255, 255, 255, 0.5);

  &__count {
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    background-color: #fff;
    vertical-align: baseline;
    padding: 16px 24px;

    span {
      font-size: 14px;
      line-height: 22px;
      font-weight: 400;
      opacity: 45%;
      margin-left: 12px;
      vertical-align: text-top;
    }
  }
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 8px 12px;
  padding: 12px;
}

.ant-pagination {
  width: fit-content;
  margin: 0 auto;
  padding: 12px 0;

  .ant-pagination-item {
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    line-height: 22px;
    width: 32px;
    height: 32px;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    background-color: #fff;

    &:not(.ant-pagination-item-active):hover {
      background-color: #fff;
      border: 1px solid #1890ff;
    }

    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      color: #000;

      &:hover {
        color: #1890ff;
      }
    }

    &.ant-pagination-item-active {
      color: #1890ff;
      border: 1px solid #1890ff;

      a {
        color: #1677ff;
      }
    }
  }
}

.ant-pagination .ant-pagination-jump-prev,
.ant-pagination .ant-pagination-jump-next {
  width: 32px;
  height: 32px;

  a {
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 2px;
    color: rgba(0, 0, 0, 0.25);
  }
}

.ant-pagination .ant-pagination-prev,
.ant-pagination .ant-pagination-next {
  width: 32px;
  height: 32px;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  border-radius: 2px;

  &:hover:not(.ant-pagination-disabled) {
    border: 1px solid #1890ff;
  }

  a[disabled] {
    opacity: 0.5;
  }
}

.loader-wrapper {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}

.error-wrapper,
.not-found-wrapper {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  background-color: #fff;
  margin-top: 12px;
  padding: 40px 0;
  display: flex;
  justify-content: center;
}
